@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight:  400;
    font-style: normal;
}
body{
  margin: 0;
}
.App {
  text-align: center;
  height: 100vh;
  background-image: linear-gradient(#ddd,#cdeecf);
    font-family: 'Pretendard-Regular';
}
button{
  background-color: lavender;
  border: 5px solid lavenderblush;
  font-family: 'Pretendard-Regular';
  cursor: pointer;
}
.input-container {
  padding: 1rem;
  display: flex;
  justify-content: center;
}

.input-text {
  padding: 1rem;
  border: 2px solid black;
  border-radius: 0.5rem;
  background: none;
  color: black;
}

.input-button {
  padding: 0.5rem;
  border: 2px solid black;
  border-radius: 1rem;
  background: none;
  cursor: pointer;
}

.nav {
  display: flex;
  width: 100%;
  background-color: rgb(206, 238, 183);
  color: black;
  padding-left: 20px;
}

.container {
  display: flex;
  width: 90%;
  margin: 0 auto;
  border-radius: 1rem;
  background-color: #fff;

}

.left {
  width: 50%;
  border: 1px solid black;
  border-radius: 1rem;
  padding: 1rem;
}

.left span {
  cursor: pointer;
}

.right {
  width: 50%;
  border: 1px solid black;
  border-radius: 1rem;
  padding: 1rem;
  margin-left: 1rem;
}

div {
  box-sizing: border-box;
}

.list {
  padding-left: 1rem;
  text-align: left;
  border: 1px solid grey;
  margin-top: 0.5rem;

}

.list h5 {
  color: gray;
}

.modal {
  padding: 2rem;
  height: 100%;
  background: #eee;
  text-align: left;
}

.modal button {
  cursor: pointer;
}